  /* styles.css */
/* Default light mode styles */
.app {
  text-align: center;
  background-color: rgb(126, 156, 191);
  padding: 20px;
  border-radius: 5px;
  height:94vh;
}

.header {
  font-size: 2rem;
  color: #333;
}

.input {
  font-size: 1rem;
  padding: 10px;
  border: 2px solid #333;
  border-radius: 5px;
}

/* Dark mode styles */
.dark-mode {
  background: linear-gradient(to bottom, #2b344a, #0a0d17);
  color: #fff;
}

.dark-text {
  color: #fff;
}

.dark-input {
  background-color: #333;
  color: #fff;
}

.dark-mode-switch {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  color: #fff;
}

