/* styles.css */
.dark-mode-switch {
    display: flex;
    align-items: center;
    color: #ded2d2;
  }
  
  .dark-mode-toggle {
    display: inline-block;
    width: 60px;
    height: 34px;
    position: relative;
    background-color: #d0cfcf;
    border-radius: 34px;
    cursor: pointer;
    margin-left: 10px;
  }
  
  .dark-mode-toggle::before {
    content: "";
    position: absolute;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    left: 4px;
    top: 4px;
    background-color: #112042;
    transition: 0.2s;
  }
  
  input[type="checkbox"] {
    display: none;
  }
  
  input[type="checkbox"]:checked + .dark-mode-toggle::before {
    transform: translateX(26px);
  }
  